import { DEFAULT_USER_ID, getActiveUser } from './userIdentity';
import { buildApiUrl } from './userStateSync';
const handleResponse = async (response) => {
    if (response.ok) {
        if (response.status === 204) {
            return undefined;
        }
        try {
            return await response.json();
        }
        catch {
            return undefined;
        }
    }
    let message = `Request failed with status ${response.status}`;
    try {
        const payload = (await response.json());
        if (payload?.error) {
            message = payload.error;
        }
    }
    catch {
        // ignore
    }
    throw new Error(message);
};
const getActiveUserId = () => {
    const user = getActiveUser();
    return user.userId ?? DEFAULT_USER_ID;
};
export const sendFriendRequest = async (targetUserId, requesterName, targetName) => {
    const userId = getActiveUserId();
    if (!targetUserId || userId === DEFAULT_USER_ID) {
        throw new Error('    ');
    }
    const response = await fetch(buildApiUrl(`/api/user/${encodeURIComponent(userId)}/friends/request`), {
        method: 'POST',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            targetUserId,
            requesterName: requesterName ?? undefined,
            targetName: targetName ?? undefined,
        }),
    });
    const payload = (await handleResponse(response));
    return payload.social;
};
export const respondToFriendRequest = async (requestId, action, responderName) => {
    const userId = getActiveUserId();
    if (!requestId || userId === DEFAULT_USER_ID) {
        throw new Error('    ');
    }
    const response = await fetch(buildApiUrl(`/api/user/${encodeURIComponent(userId)}/friends/request/${encodeURIComponent(requestId)}/respond`), {
        method: 'POST',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            action: action === 'accepted' ? 'accept' : 'decline',
            responderName: responderName ?? undefined,
        }),
    });
    const payload = (await handleResponse(response));
    return payload.social;
};
export const updateFriendSharing = async (friendId, shareMyStatsWith) => {
    const userId = getActiveUserId();
    if (!friendId || userId === DEFAULT_USER_ID) {
        throw new Error('    ');
    }
    const response = await fetch(buildApiUrl(`/api/user/${encodeURIComponent(userId)}/friends/${encodeURIComponent(friendId)}/sharing`), {
        method: 'POST',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            shareMyStatsWith,
        }),
    });
    const payload = (await handleResponse(response));
    return payload.social;
};
export const fetchSharedUserData = async (targetUserId) => {
    const viewerId = getActiveUserId();
    if (!targetUserId || viewerId === DEFAULT_USER_ID) {
        throw new Error('    ');
    }
    const response = await fetch(buildApiUrl(`/api/user/${encodeURIComponent(targetUserId)}/shared/activity?viewer=${encodeURIComponent(viewerId)}`), {
        method: 'GET',
        credentials: 'include',
    });
    const payload = (await handleResponse(response));
    return {
        activityData: payload.activityData ?? {},
        homeState: payload.homeState ?? null,
    };
};
export const removeFriend = async (friendId) => {
    const userId = getActiveUserId();
    if (!friendId || userId === DEFAULT_USER_ID) {
        throw new Error('    ');
    }
    const response = await fetch(buildApiUrl(`/api/user/${encodeURIComponent(userId)}/friends/${encodeURIComponent(friendId)}`), {
        method: 'DELETE',
        credentials: 'include',
    });
    const payload = (await handleResponse(response));
    return payload.social;
};
