___CSS_LOADER_EXPORT___.push([module.id, `.people-page {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 24px 20px 32px;
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  overflow-x: hidden;
}

.people-name {
  display: flex;
  gap: 8px;
  align-items: baseline;
  padding: 16px 20px;
  margin: -24px -20px 0;
  background-color: #ffffff;
  border-bottom: 1px solid #e5e7eb;
}

.people-name__first {
  font-size: 24px;
  font-weight: 700;
  color: #1d1d1d;
}

.people-name__last {
  font-size: 24px;
  font-weight: 700;
  color: #1d1d1d;
}

.people-header {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 16px;
  border-radius: 12px;
  background-color: #ffffff;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.06);
}

.people-header__actions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.people-id {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.people-id__wrapper {
  display: flex;
  align-items: center;
  gap: 12px;
}

.people-id__caption {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #6b6b6b;
}

.people-id__value {
  font-size: 20px;
  font-weight: 600;
  color: #1d1d1d;
  word-break: break-all;
  flex: 1;
}

.people-button {
  border: none;
  border-radius: 8px;
  padding: 10px 14px;
  background-color: #f1f3f5;
  color: #1d1d1d;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

.people-button:hover:not(:disabled) {
  background-color: #e5e8ec;
}

.people-button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.people-button--primary {
  background-color: #3b82f6;
  color: #ffffff;
}

.people-button--primary:hover:not(:disabled) {
  background-color: #2563eb;
}

.people-button--secondary {
  background-color: #fbe7e7;
  color: #b91c1c;
}

.people-button--danger {
  background-color: #dc2626;
  color: #ffffff;
}

.people-button--danger:hover:not(:disabled) {
  background-color: #b91c1c;
}

.people-button--copy {
  flex-shrink: 0;
  margin: 0;
}

.people-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 16px;
  border-radius: 12px;
  background-color: #ffffff;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.06);
}

.people-section__title {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  color: #1d1d1d;
}

.people-requests {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.people-requests__group ul {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.people-requests__subtitle {
  margin: 0 0 8px 0;
  font-size: 14px;
  font-weight: 600;
  color: #374151;
}

.people-requests__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border-radius: 10px;
  background-color: #f9fafb;
}

.people-requests__item--outgoing {
  flex-direction: column;
  align-items: flex-start;
}

.people-requests__info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.people-requests__name {
  font-size: 15px;
  font-weight: 600;
  color: #111827;
}

.people-requests__id,
.people-friends__id {
  font-size: 12px;
  color: #6b7280;
}

.people-requests__actions {
  display: flex;
  gap: 8px;
}

.people-requests__status {
  font-size: 13px;
  color: #6b7280;
}

.people-friends {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.people-friends__item {
  width: 100%;
  border: none;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 16px;
  border-radius: 12px;
  background-color: #f9fafb;
  cursor: pointer;
  transition: box-shadow 0.2s ease, transform 0.2s ease;
  text-align: left;
}

.people-friends__item:hover {
  box-shadow: 0 14px 32px rgba(37, 99, 235, 0.15);
  transform: translateY(-1px);
}

.people-friends__info {
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex: 1;
}

.people-friends__name {
  font-size: 16px;
  font-weight: 600;
  color: #111827;
}

.people-friends__badges {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 6px;
}

.people-friends__chevron {
  font-size: 20px;
  color: #9ca3af;
}

.people-tag {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  font-weight: 600;
  padding: 4px 10px;
  border-radius: 999px;
  color: #64748b;
  background-color: rgba(148, 163, 184, 0.18);
}

.people-tag::before {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: currentColor;
}

.people-tag--success {
  color: #16a34a;
  background-color: rgba(22, 163, 74, 0.12);
}

.people-tag--inactive {
  color: #9ca3af;
  background-color: rgba(156, 163, 175, 0.16);
}

.people-empty {
  font-size: 14px;
  color: #6b7280;
}

.people-feedback {
  border-radius: 10px;
  padding: 12px 14px;
  font-size: 14px;
}

.people-feedback--success {
  background-color: #dcfce7;
  color: #047857;
}

.people-feedback--error {
  background-color: #fee2e2;
  color: #b91c1c;
}

.people-notifications {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.people-notifications__item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.people-notifications__dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #3b82f6;
  margin-top: 6px;
}

.people-notifications__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.people-notifications__time {
  font-size: 12px;
  color: #9ca3af;
}

@media (min-width: 768px) {
  .people-header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .people-friends__item {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .people-friends__controls {
    align-items: flex-end;
  }
}


