___CSS_LOADER_EXPORT___.push([module.id, `.subjects-page {
  padding: 24px 20px 32px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  overflow-x: hidden;
}

.subjects-header {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.subjects-title {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  color: #0f172a;
}

.subjects-subtitle {
  margin: 0;
  font-size: 14px;
  color: #64748b;
  line-height: 1.4;
}

.subjects-card {
  background: #ffffff;
  border-radius: 18px;
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.12);
  padding: 18px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.subjects-card--currency {
  border-left: 4px solid #f59e0b;
}

.subjects-card__label {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: #94a3b8;
  font-weight: 600;
}

.subjects-card__value {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 28px;
  font-weight: 700;
  color: #0f172a;
}

.subjects-card__value span[role='img'] {
  font-size: 28px;
}

.subjects-card__hint {
  margin: 0;
  font-size: 13px;
  color: #64748b;
}

.subjects-achievements {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.subjects-achievement {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  padding: 12px;
  border-radius: 14px;
  background: #f8fafc;
  transition: border 0.2s ease, background 0.2s ease;
}

.subjects-achievement--unlocked {
  border: 1px solid rgba(34, 197, 94, 0.5);
  background: rgba(34, 197, 94, 0.12);
}

.subjects-achievement__marker {
  font-size: 16px;
  color: #94a3b8;
  margin-top: 3px;
}

.subjects-achievement--unlocked .subjects-achievement__marker {
  color: #22c55e;
}

.subjects-achievement__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.subjects-achievement__title {
  font-size: 15px;
  font-weight: 600;
  color: #1f2937;
}

.subjects-achievement__description {
  font-size: 13px;
  color: #64748b;
  line-height: 1.4;
}

.subjects-achievement__timestamp {
  font-size: 12px;
  color: #475569;
}

.subjects-version {
  margin-top: auto;
  padding-top: 24px;
  text-align: center;
  font-size: 12px;
  color: #cbd5e1;
}

@media (max-width: 420px) {
  .subjects-card__value {
    font-size: 24px;
  }
}


