___CSS_LOADER_EXPORT___.push([module.id, `.stats-page {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  overflow-x: hidden;
}

.stats-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}

.stats-title {
  margin: 0;
  font-size: 22px;
  font-weight: 700;
  color: #0f172a;
}

.stats-user-selector {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stats-user-selector__label {
  font-size: 14px;
  color: #475569;
  font-weight: 600;
}

.stats-user-selector__select {
  border-radius: 12px;
  border: 1px solid #cbd5f5;
  padding: 10px 12px;
  font-size: 14px;
  font-weight: 500;
  outline: none;
  background-color: #ffffff;
  transition: border-color 0.2s ease;
}

.stats-user-selector__select:focus {
  border-color: #2563eb;
}

.stats-user-selector__hint {
  font-size: 13px;
  color: #6b7280;
}

.stats-user-selector__error {
  color: #dc2626;
}

.stats-summary {
  display: flex;
  flex-direction: row;
  gap: 12px;
  flex-shrink: 0;
}

.stats-summary__item {
  background: #f8fafc;
  border-radius: 14px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex: 1;
}

.stats-summary__item--interactive {
  cursor: pointer;
  transition: background 0.2s ease, box-shadow 0.2s ease;
  border: none;
  text-align: left;
}

.stats-summary__item--interactive:hover {
  background: #e8f0ff;
  box-shadow: 0 10px 24px rgba(37, 99, 235, 0.15);
}

.stats-summary__label {
  font-size: 12px;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.4px;
}

.stats-summary__value {
  font-size: 16px;
  font-weight: 600;
  color: #0f172a;
}

.stats-chart-card {
  background: #ffffff;
  border-radius: 18px;
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.12);
  padding: 16px;
  min-height: 240px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

.stats-empty {
  text-align: center;
  color: #64748b;
  font-size: 14px;
}

@media (max-width: 420px) {
  .stats-summary {
    flex-direction: column;
  }
}

