___CSS_LOADER_EXPORT___.push([module.id, `.home-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: calc(100vh - 80px);
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  padding: 24px 0 calc(80px + 20px);
  margin-bottom: -80px;
  text-align: center;
  transition: background-color 0.3s ease;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.home-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  width: calc(100% - 32px);
  max-width: 360px;
  flex-shrink: 0;
  background: transparent;
  padding: 16px 20px;
}

.home-title {
  font-size: 28px;
  font-weight: 700;
  color: #0f172a;
  margin: 0;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8), 0 0 8px rgba(255, 255, 255, 0.5);
}

.home-goal-button {
  background: #1a73e8;
  color: #ffffff;
  border: none;
  border-radius: 18px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  width: 100%;
  max-width: 280px;
  transition: background 0.2s ease-in-out, transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  box-shadow: 0 4px 12px rgba(26, 115, 232, 0.4), 0 2px 4px rgba(0, 0, 0, 0.2);
}

.home-goal-button:active {
  transform: scale(0.98);
}

.home-goal-button:hover {
  background: #1866d2;
  box-shadow: 0 6px 16px rgba(26, 115, 232, 0.5), 0 3px 6px rgba(0, 0, 0, 0.25);
}

.home-goal-status {
  font-size: 18px;
  font-weight: 600;
  color: #0f172a;
  padding: 12px 18px;
  border-radius: 14px;
  background: rgba(26, 115, 232, 0.15);
  width: 100%;
  max-width: 320px;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.home-goal-status--success {
  background: rgba(16, 185, 129, 0.2);
  color: #065f46;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
}

.home-goal-progress {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  font-weight: 600;
  color: #1e293b;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
}

.home-illustration {
  margin-top: auto;
  display: flex;
  justify-content: center;
  width: 100%;
  flex-shrink: 1;
  margin-bottom: auto;
  min-height: 0;
  transform: translateY(35px); /*          */
}

.home-illustration-avatar {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: min(220px, 100%);
}

.home-illustration-image {
  max-width: 200px;
  width: 100%;
  height: auto;
  max-height: 200px;
  object-fit: contain;
}

.home-illustration-hat {
  position: absolute;
  width: 70%;
  max-width: 150px;
  left: 50%;
  bottom: calc(60% - 35px); /*       (70px = 150px/2 ) */
  transform: translateX(-50%);
  pointer-events: none;
}

.home-actions {
  display: flex;
  justify-content: center;
  margin-top: auto;
  margin-bottom: 0;
  flex-shrink: 0;
  padding: 0 16px;
  width: 100%;
  transform: translateY(35px); /*          */
}

.home-customize-button {
  border: none;
  border-radius: 18px;
  padding: 12px 20px;
  background: #3b82f6;
  color: #ffffff;
  font-weight: 700;
  font-size: 15px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: background 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4), 0 2px 4px rgba(0, 0, 0, 0.2);
}

.home-customize-button:hover {
  background: #2563eb;
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.5), 0 3px 6px rgba(0, 0, 0, 0.25);
}

.home-customize-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ef4444;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
}

@media (max-width: 380px) {
  .home-page {
    padding: 20px 0 calc(80px + 16px);
  }

  .home-header {
    padding: 14px 16px;
    gap: 14px;
  }

  .home-title {
    font-size: 24px;
  }

  .home-goal-status {
    font-size: 16px;
  }

  .home-customize-button {
    width: 100%;
    justify-content: center;
  }
  
  .home-illustration-image {
    max-width: 180px;
    max-height: 180px;
  }
}





