___CSS_LOADER_EXPORT___.push([module.id, `.calendar-modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  z-index: 2000;
}

.calendar-modal {
  background: #ffffff;
  border-radius: 16px;
  width: 100%;
  max-width: 420px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 24px 48px rgba(15, 23, 42, 0.2);
}

.calendar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px 12px;
  border-bottom: 1px solid rgba(15, 23, 42, 0.08);
}

.calendar-title {
  font-size: 18px;
  font-weight: 600;
  text-transform: capitalize;
}

.calendar-nav-button {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 8px;
  color: #1f2937;
  border-radius: 8px;
  transition: background 0.2s ease;
}

.calendar-nav-button:hover {
  background: rgba(59, 130, 246, 0.12);
}

.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  padding: 12px 16px;
  gap: 8px;
  font-size: 12px;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.calendar-grid {
  display: flex;
  flex-direction: column;
  padding: 0 16px 16px;
  gap: 8px;
  overflow-y: auto;
}

.calendar-week {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
}

.calendar-day {
  position: relative;
  border: none;
  border-radius: 12px;
  min-height: 56px;
  padding: 8px;
  text-align: left;
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.15s ease, border 0.15s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #111827;
  background: #ffffff;
  box-shadow: inset 0 0 0 1px rgba(15, 23, 42, 0.04);
}

.calendar-day--other-month {
  opacity: 0.45;
}

.calendar-day--selected {
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.6);
}

.calendar-day--today {
  border: 1px solid rgba(59, 130, 246, 0.8);
}

.calendar-day:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(15, 23, 42, 0.12);
}

.calendar-day__number {
  font-size: 16px;
  font-weight: 600;
}

.calendar-day__productive {
  font-size: 11px;
  font-weight: 500;
  opacity: 0.85;
}

.calendar-footer {
  padding: 12px 20px 16px;
  border-top: 1px solid rgba(15, 23, 42, 0.08);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.calendar-legend {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  font-size: 12px;
  color: #4b5563;
}

.calendar-legend__item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.calendar-legend__color {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1px rgba(15, 23, 42, 0.1);
}

.calendar-close-button {
  align-self: flex-end;
  background: #2563eb;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0 12px 24px rgba(37, 99, 235, 0.22);
  transition: background 0.2s ease, transform 0.2s ease;
}

.calendar-close-button:hover {
  background: #1d4ed8;
  transform: translateY(-1px);
}

.calendar-close-button:active {
  transform: translateY(0);
}

@media (max-width: 420px) {
  .calendar-modal {
    border-radius: 12px;
  }
}


